.home-page {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

h5 {
    margin: 10px 0;
}

.site-switch {
    width: 100%;
    margin: 0 0 40px 0;
}

.site-switch-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
}

.site-switch span {
    line-height: 1px;
    display: block;
    flex-grow: 1;
    text-align: right;
    padding: 0 5px;
}

.site-switch span:nth-child(3) {
    text-align: left;
}

.switch {
    position: relative;
    display: block;
    width: 60px;
    height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.slider {
    position: absolute;
    cursor: pointer;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    background-color: #4D19BC;
    -webkit-transition: .4s;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked+.slider {
    background-color: #4D19BC;
}

input:focus+.slider {
    box-shadow: 0 0 1px #4D19BC;
}

input:checked+.slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}